
'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Video, VideoOff, AlertCircle, Loader2, Copy, LogIn, Users, UserMinus, UserPlus } from 'lucide-react'; // Added Users, UserMinus, UserPlus
import { useTranslation } from 'react-i18next';
import Link from 'next/link';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"

// Simple unique code generation (replace with a more robust solution if needed)
function generateMeetingCode(): string {
  return Math.random().toString(36).substring(2, 8).toUpperCase();
}

// Dummy names for simulation
const dummyParticipantNames = ["Alice Smith", "Bob Johnson", "Charlie Brown", "Diana Prince", "Ethan Hunt"];

export default function VideoCallPage() {
  const router = useRouter();
  const { user, loading: authLoading } = useAuth();
  const { toast } = useToast();
  const { t } = useTranslation();
  const videoRef = useRef<HTMLVideoElement>(null);
  const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null); // null initially, true/false after check
  const [isMeetingActive, setIsMeetingActive] = useState(false);
  const [meetingCode, setMeetingCode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [generatedCode, setGeneratedCode] = useState<string | null>(null);
  const [participantList, setParticipantList] = useState<string[]>([]);
  const simulationIntervalRef = useRef<NodeJS.Timeout | null>(null); // Ref to hold interval ID

  const startParticipantSimulation = useCallback(() => {
    // Clear any existing interval
    if (simulationIntervalRef.current) {
      clearInterval(simulationIntervalRef.current);
    }

    // Start a new interval
    simulationIntervalRef.current = setInterval(() => {
      setParticipantList(prevList => {
        const action = Math.random();
        if (action < 0.3 && prevList.length > 1) { // 30% chance to remove a participant (if more than 1)
          const randomIndex = Math.floor(Math.random() * (prevList.length -1)) + 1; // Avoid removing self (index 0)
          const removedName = prevList[randomIndex];
          console.log(`[Sim] Participant left: ${removedName}`);
          toast({ title: t('page.videoCall.toast.participantLeft', { name: removedName }), variant: 'default' });
          return prevList.filter((_, index) => index !== randomIndex);
        } else if (action < 0.7) { // 40% chance to add a participant
          const availableNames = dummyParticipantNames.filter(name => !prevList.includes(name));
          if (availableNames.length > 0) {
            const randomIndex = Math.floor(Math.random() * availableNames.length);
            const newName = availableNames[randomIndex];
            console.log(`[Sim] Participant joined: ${newName}`);
            toast({ title: t('page.videoCall.toast.participantJoined', { name: newName }), variant: 'default' });
            return [...prevList, newName];
          }
        }
        // 30% chance to do nothing
        return prevList;
      });
    }, 7000); // Simulate join/leave every 7 seconds
  }, [t]);

  const stopParticipantSimulation = useCallback(() => {
    if (simulationIntervalRef.current) {
      clearInterval(simulationIntervalRef.current);
      simulationIntervalRef.current = null;
      console.log("[Sim] Participant simulation stopped.");
    }
  }, []);

  const requestCameraPermission = useCallback(async (onSuccess?: () => void) => {
     setIsLoading(true);
     try {
       const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true }); // Request both audio and video
       setHasCameraPermission(true);
       if (videoRef.current) {
         videoRef.current.srcObject = stream;
       }
       console.log("Camera access granted.");
       onSuccess?.(); // Call success callback if provided

     } catch (error) {
       console.error('Error accessing camera/microphone:', error);
       setHasCameraPermission(false);
       toast({
         variant: 'destructive',
         title: t('page.videoCall.error.permissionDeniedTitle'),
         description: t('page.videoCall.error.permissionDenied'),
       });
     } finally {
         setIsLoading(false);
     }
   }, [toast, t]);


  // Effect to request permission only if needed and meeting is intended to start
   useEffect(() => {
       // Cleanup function to stop media stream and simulation when component unmounts or meeting ends
       return () => {
           if (videoRef.current && videoRef.current.srcObject) {
               const stream = videoRef.current.srcObject as MediaStream;
               stream.getTracks().forEach(track => track.stop());
               console.log("Media stream stopped.");
           }
           stopParticipantSimulation(); // Stop simulation on unmount
       };
   }, [stopParticipantSimulation]);


  const handleJoinMeeting = async () => {
    if (!meetingCode.trim()) {
      toast({ variant: 'destructive', title: t('page.videoCall.error.codeRequired') });
      return;
    }

    const joinAction = () => {
      console.log(`Joining meeting with code: ${meetingCode}`);
      // In a real app, you'd validate the code and connect to a WebRTC service
      setIsMeetingActive(true);
      if(user?.name) {
        setParticipantList([user.name]); // Add self to list
      }
      startParticipantSimulation(); // Start simulation on join
      toast({ title: t('page.videoCall.toast.joiningTitle'), description: t('page.videoCall.toast.joiningDescription', { code: meetingCode }) });
    };

    // Basic validation / permission check
    if (hasCameraPermission === true) {
        joinAction(); // Permission already granted
    } else if (hasCameraPermission === false) {
        toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
    } else {
         // Request permission first, then join on success
         await requestCameraPermission(joinAction);
    }
  };

   const handleGenerateCode = async () => {
       const newCode = generateMeetingCode();
       setGeneratedCode(newCode);
       setMeetingCode(newCode); // Pre-fill code

       const startAction = () => {
           console.log(`Starting new meeting with code: ${newCode}`);
           setIsMeetingActive(true);
           if (user?.name) {
               setParticipantList([user.name]); // Add self to list
           }
           startParticipantSimulation(); // Start simulation on start
           toast({ title: t('page.videoCall.toast.startedTitle'), description: t('page.videoCall.toast.startedDescription', { code: newCode }) });
       };

       if (hasCameraPermission === true) {
            startAction();
       } else if (hasCameraPermission === false) {
            toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
            setGeneratedCode(null); // Reset generated code if permission denied
       } else {
            await requestCameraPermission(startAction);
       }
   };


  const handleLeaveMeeting = () => {
    setIsMeetingActive(false);
    setMeetingCode('');
    setGeneratedCode(null);
    setParticipantList([]); // Clear participants
    stopParticipantSimulation(); // Stop simulation on leave
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream;
      stream.getTracks().forEach(track => track.stop());
      videoRef.current.srcObject = null; // Clear the srcObject
    }
    toast({ title: t('page.videoCall.toast.leftMeeting') });
  };

  const copyCodeToClipboard = () => {
    if (meetingCode) { // Copy the current meeting code
      navigator.clipboard.writeText(meetingCode)
        .then(() => {
          toast({ title: t('page.videoCall.toast.codeCopied') });
        })
        .catch(err => {
          console.error('Failed to copy meeting code: ', err);
          toast({ variant: 'destructive', title: t('page.videoCall.error.copyFailed') });
        });
    }
  };

  // Loading state for auth
  if (authLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  // Redirect if not logged in
  if (!user) {
    return (
      <div className="text-center py-10">
        <AlertCircle className="mx-auto h-12 w-12 text-destructive mb-4" />
        <h1 className="text-2xl font-semibold mb-2">{t('toast.accessDenied.title')}</h1>
        <p className="text-muted-foreground mb-6">{t('accessDenied.loginRequired.page', { pageName: t('header.videoCall') })}</p>
        <Link href="/login">
          <Button><LogIn className="mr-2 h-4 w-4" /> {t('login.loginButton')}</Button>
        </Link>
      </div>
    );
  }


  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl font-bold tracking-tight flex items-center">
            <Video className="mr-3 h-8 w-8 text-primary" />
            {t('page.videoCall.title')}
          </CardTitle>
          <CardDescription>
            {isMeetingActive
              ? t('page.videoCall.description.inMeeting', { code: meetingCode })
              : t('page.videoCall.description.joinOrCreate')}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {isMeetingActive ? (
            // --- Meeting Active View ---
            <div className="space-y-4">
              <div className="aspect-video bg-muted rounded-lg overflow-hidden relative border">
                <video ref={videoRef} className="w-full h-full object-cover" autoPlay muted playsInline />
                 {/* Loading/Permission States Overlays */}
                {hasCameraPermission === false && (
                     <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/70 text-destructive-foreground p-4 text-center">
                        <VideoOff className="h-16 w-16 mb-4" />
                        <p className="text-lg font-semibold">{t('page.videoCall.error.permissionDeniedTitle')}</p>
                        <p>{t('page.videoCall.error.permissionInstructions')}</p>
                    </div>
                )}
                 {hasCameraPermission === null && isLoading && (
                     <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/70 text-white p-4">
                        <Loader2 className="h-12 w-12 animate-spin mb-4" />
                        <p>{t('page.videoCall.requestingPermission')}</p>
                    </div>
                )}
              </div>

              {/* Meeting Info & Controls Row */}
              <div className="flex flex-col sm:flex-row justify-between items-center gap-4 p-4 border rounded-md bg-muted/50">
                 {/* Participant Info & Dropdown */}
                 <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                        <Button variant="outline">
                            <Users className="mr-2 h-4 w-4" />
                            {t('page.videoCall.participants', { count: participantList.length })}
                        </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="start" className="w-64">
                        <DropdownMenuLabel>{t('page.videoCall.participantListTitle')}</DropdownMenuLabel>
                        <DropdownMenuSeparator />
                        {participantList.length > 0 ? (
                            participantList.map((name, index) => (
                                <DropdownMenuItem key={index} className="flex items-center gap-2">
                                    {/* Basic icon, could be enhanced */}
                                    {index === 0 ? <UserPlus className="h-4 w-4 text-green-500" /> : <UserMinus className="h-4 w-4 text-red-500" />}
                                    <span className="truncate">{name} {index === 0 ? `(${t('page.videoCall.you')})` : ''}</span>
                                </DropdownMenuItem>
                            ))
                        ) : (
                            <DropdownMenuItem disabled>{t('page.videoCall.noParticipants')}</DropdownMenuItem>
                        )}
                    </DropdownMenuContent>
                </DropdownMenu>

                 {/* Copy Code Button */}
                 <div className="flex items-center gap-2">
                    <span className="text-sm text-muted-foreground">{t('page.videoCall.meetingCodeLabel')}:</span>
                    <span className="font-mono font-semibold text-primary">{meetingCode}</span>
                    <Button variant="ghost" size="icon" onClick={copyCodeToClipboard} aria-label={t('page.videoCall.button.copyCode')}>
                        <Copy className="h-4 w-4" />
                    </Button>
                 </div>
              </div>


              {/* Placeholder for other meeting controls */}
               <div className="text-center text-muted-foreground text-sm p-4 border rounded-md">
                  {t('page.videoCall.placeholder.controls')}
               </div>

              {/* Leave Meeting Button */}
              <Button onClick={handleLeaveMeeting} variant="destructive" className="w-full">
                <VideoOff className="mr-2 h-4 w-4" />
                {t('page.videoCall.button.leaveMeeting')}
              </Button>
            </div>
          ) : (
            // --- Pre-Meeting View ---
            <div className="flex flex-col md:flex-row gap-6 items-stretch">
              {/* Join Meeting Card */}
              <Card className="flex-1 flex flex-col">
                <CardHeader>
                  <CardTitle>{t('page.videoCall.joinTitle')}</CardTitle>
                  <CardDescription>{t('page.videoCall.joinDescription')}</CardDescription>
                </CardHeader>
                <CardContent className="flex-grow">
                  <div className="space-y-2">
                    <Label htmlFor="meetingCode">{t('page.videoCall.label.meetingCode')}</Label>
                    <Input
                      id="meetingCode"
                      placeholder={t('page.videoCall.placeholder.meetingCode')}
                      value={meetingCode}
                      onChange={(e) => setMeetingCode(e.target.value.toUpperCase())} // Ensure uppercase
                      maxLength={6} // Match generated code length
                      className="font-mono tracking-widest text-center"
                      disabled={isLoading}
                    />
                  </div>
                </CardContent>
                <CardFooter>
                  <Button onClick={handleJoinMeeting} className="w-full" disabled={isLoading || !meetingCode || meetingCode.length !== 6}>
                    {isLoading && !generatedCode ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <LogIn className="mr-2 h-4 w-4" />}
                    {t('page.videoCall.button.joinMeeting')}
                  </Button>
                </CardFooter>
              </Card>

              {/* Start Meeting Card (Court Official Only) */}
              {user.role === 'CourtOfficial' && (
                <Card className="flex-1 border-primary bg-primary/5 flex flex-col">
                  <CardHeader>
                    <CardTitle>{t('page.videoCall.startTitle')}</CardTitle>
                    <CardDescription>{t('page.videoCall.startDescription')}</CardDescription>
                  </CardHeader>
                  <CardContent className="text-center flex-grow flex items-center justify-center">
                    {/* Content is handled by the button below */}
                     <Button onClick={handleGenerateCode} className="w-full" disabled={isLoading}>
                         {isLoading && generatedCode ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Video className="mr-2 h-4 w-4" />}
                         {t('page.videoCall.button.startMeeting')}
                    </Button>
                  </CardContent>
                   <CardFooter>
                       {/* Footer can be empty or used for additional info */}
                   </CardFooter>
                </Card>
              )}
            </div>
          )}

          {/* Camera Permission Alert - Shown only before joining/starting */}
          {hasCameraPermission === false && !isMeetingActive && (
            <Alert variant="destructive" className="mt-6">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>{t('page.videoCall.error.permissionDeniedTitle')}</AlertTitle>
              <AlertDescription>
                {t('page.videoCall.error.permissionInstructions')}
                 <Button variant="link" className="p-0 h-auto ml-1 text-destructive-foreground underline" onClick={() => requestCameraPermission()}>
                    {t('page.videoCall.button.retryPermission')}
                 </Button>
              </AlertDescription>
            </Alert>
          )}
           {/* General Instructions/Info */}
          {!isMeetingActive && (
             <Alert variant="default" className="mt-6">
               <Video className="h-4 w-4" />
               <AlertTitle>{t('page.videoCall.infoTitle')}</AlertTitle>
               <AlertDescription>
                   {t('page.videoCall.infoText')}
                   {user.role === 'CourtOfficial' ? ` ${t('page.videoCall.infoTextOfficial')}` : ` ${t('page.videoCall.infoTextParticipant')}`}
               </AlertDescription>
             </Alert>
          )}
        </CardContent>
      </Card>
    </div>
  );
}

